interface HistoryModalProps {
  isOpen: boolean;
  onClose: () => void;
  addToCart: (item: any) => void;
  modals: Array<{
    selectedImage: string;
    leftItemsDict: Record<string, ItemProps[]>;
    rightItemsDict: Record<string, ItemProps[]>;
  }>;
  removeFromCart: (item: any) => void;
  cartItems?: ItemProps[];
}

const HistoryModal: React.FC<HistoryModalProps> = ({
  isOpen,
  onClose,
  addToCart,
  modals,
  removeFromCart,
  cartItems,
}) => {
  // Prevent scrolling of background
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden"; // Disable background scrolling
    } else {
      document.body.style.overflow = ""; // Reset to default
    }

    // Cleanup when modal is closed or component unmounts
    return () => {
      document.body.style.overflow = "";
    };
  }, [isOpen]);

  if (!isOpen) return null;

  return (
    <Box
      position="fixed"
      top="0"
      left="0"
      width="100%"
      height="100%"
      zIndex="1000"
      bg="rgba(0, 0, 0, 0.8)"
      backdropFilter="blur(10px)"
      overflow="hidden"
      color="white"
    >
      {/* Close Button Below Navbar */}
      <Box
        as="button"
        onClick={onClose}
        position="absolute"
        top="50px" // Adjust based on navbar height
        right="16px"
        width="40px"
        height="40px"
        borderRadius="full"
        bg="white"
        color="black"
        display="flex"
        alignItems="center"
        justifyContent="center"
        boxShadow="lg"
        zIndex="1001"
        _hover={{ bg: "gray.200" }}
      >
        <X size={24} />
      </Box>

      {/* Content */}
      <Box
        position="absolute"
        top="20px" // Adjust for spacing below the close button
        left="0"
        right="0"
        bottom="0"
        overflowY="auto"
        mt="20px"
        pt="20px"
      >
        <Flex direction="column" alignItems="center" gap="10">
          {modals.map((modal, index) => (
            <Box
              key={index}
              bg="white"
              color="black"
              width="80%"
              borderRadius="12px"
              boxShadow="md"
              overflow="hidden" // Ensures content respects border-radius
            >
              <WannaBeModalInnerComponent
                selectedImage={modal.selectedImage}
                leftItemsDict={modal.leftItemsDict}
                rightItemsDict={modal.rightItemsDict}
                addToCart={addToCart}
                removeFromCart={removeFromCart}
                showCloseButton={false}
                shouldShowRefreshIcon={true}
                shouldShowBookmarkIcon={true}
                cartItems={cartItems}
              />
            </Box>
          ))}
        </Flex>
      </Box>
    </Box>
  );
};

export default HistoryModal;
