import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
} from "@chakra-ui/react";

interface WannaBeModalWrapperProps {
  isOpen: boolean;
  onClose: () => void;
  children: React.ReactNode;
  size?: string;
  maxWidth?: string;
}

const WannaBeModalWrapper: React.FC<WannaBeModalWrapperProps> = ({
  isOpen,
  onClose,
  children,
  size = "6xl",
  maxWidth,
}) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose} size={size}>
      <ModalOverlay backdropFilter="blur(10px)" bg="rgba(0, 0, 0, 0.4)" />
      <ModalContent
        maxWidth={maxWidth}
        overflow="hidden"
        borderRadius="xl"
        boxShadow="0 6px 16px rgba(0, 0, 0, 0.25), 0 8px 30px rgba(0, 0, 0, 0.2)"
        border="2px solid"
        borderColor="gray.200"
        position="relative"
        bg="white"
      >
        <ModalBody padding={0}>{children}</ModalBody>
      </ModalContent>
    </Modal>
  );
};

export default WannaBeModalWrapper;
