import React, { useState, useCallback, useEffect } from "react";
import { Box, Image } from "@chakra-ui/react";
import BookmarkLogo from "../assets/BookmarkLogoNoBoarder.png";

const FloatingDraggableBookmarks: React.FC<{ cartItemsCount: number; onClick: () => void }> = ({
  cartItemsCount,
  onClick,
}) => {
  const [position, setPosition] = useState(() => {
    const initialX = window.innerWidth - 80;
    const initialY = window.innerHeight - 80;
    return { x: initialX, y: initialY };
  });
  const [dragging, setDragging] = useState(false);
  const [start, setStart] = useState({ x: 0, y: 0 });
  const [hasMoved, setHasMoved] = useState(false);

  const handleMouseDown = (e: React.MouseEvent) => {
    e.preventDefault();
    setDragging(true);
    setHasMoved(false); // Reset the moved flag
    setStart({ x: e.clientX - position.x, y: e.clientY - position.y });
  };

  const handleMouseMove = useCallback(
    (e: MouseEvent) => {
      if (dragging) {
        e.preventDefault();
        setPosition({
          x: e.clientX - start.x,
          y: e.clientY - start.y,
        });

        // Mark as moved if the position changes
        setHasMoved(true);
      }
    },
    [dragging, start]
  );

  const handleMouseUp = useCallback(() => {
    setDragging(false);

    // Check if the component was moved; if not, treat it as a click
    if (!hasMoved) {
      onClick();
    }
  }, [hasMoved, onClick]);

  useEffect(() => {
    if (dragging) {
      window.addEventListener("mousemove", handleMouseMove);
      window.addEventListener("mouseup", handleMouseUp);
    } else {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    }

    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, [dragging, handleMouseMove, handleMouseUp]);

  return (
    <Box
      position="fixed"
      left={`${position.x}px`}
      top={`${position.y}px`}
      zIndex="1500"
      width="65px"
      height="60px"
      bg="white"
      borderRadius="15px"
      boxShadow="0px 6px 10px rgba(0, 0, 0, 0.15), 0px 2px 5px rgba(0, 0, 0, 0.1)" // Updated shadow
      display="flex"
      alignItems="center"
      justifyContent="center"
      cursor="grab"
      _hover={{ boxShadow: "0px 8px 12px rgba(0, 0, 0, 0.3), 0px 4px 7px rgba(0, 0, 0, 0.2)" }} // Enhanced hover effect
      onMouseDown={handleMouseDown}
      style={{ touchAction: "none" }}
    >
      <Image
        src={BookmarkLogo}
        alt="Cart Logo"
        boxSize="45px"
        objectFit="contain"
      />
      {cartItemsCount > 0 && (
        <Box
          position="absolute"
          top="-5px"
          right="-5px"
          backgroundImage="linear-gradient(150deg, #124699, #5B9EED)"
          color="white"
          width="20px"
          height="20px"
          display="flex"
          alignItems="center"
          justifyContent="center"
          borderRadius="full"
          fontSize="xs"
        >
          {cartItemsCount}
        </Box>
      )}
    </Box>
  );
};

export default FloatingDraggableBookmarks;
