import React from "react";
import ReactDOM from "react-dom/client";
import { ChakraProvider } from "@chakra-ui/react";
import App from "./App";

// Find or create a root container
const initializeRoot = (): HTMLElement => {
  let container = document.getElementById("wannabe-root");
  if (!container) {
    container = document.createElement("div");
    container.id = "wannabe-root";
    document.body.appendChild(container);
    console.log("Root container created.");
  } else {
    console.log("Root container already exists.");
  }
  return container;
};

// Render the React application
try {
  ReactDOM.createRoot(initializeRoot()).render(
    <React.StrictMode>
      <ChakraProvider>
        <App />
      </ChakraProvider>
    </React.StrictMode>
  );
  console.log("React app initialized!");
} catch (error) {
  console.error("Error rendering React app:", error);
}
