import React, { useState, useEffect } from "react";
import WannaBeModal from "./components/WannaBeModal";
import { ItemProps } from "./components/Item";
import { CartModal } from "./components/CartModal";
import HistoryModal, { mockModals } from "./components/HistoryModal";
import FloatingDraggableBookmarks from "./components/FloatingDraggableBookmarks";
import { addIconsToPhotos } from "./functions/addIconsToPhotos";

type SetSelectedImageFunction = React.Dispatch<React.SetStateAction<string | null>>;

const App: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const [cartOpen, setCartOpen] = useState(false);
  const [cartItems, setCartItems] = useState<ItemProps[]>([]);
  const [historyOpen, setHistoryOpen] = useState(false);

  // Event handlers
  const onClose = () => setIsOpen(false);
  const onOpen = () => setIsOpen(true);
  const openCart = () => setCartOpen(true);
  const closeCart = () => setCartOpen(false);
  const openHistory = () => setHistoryOpen(true);
  const closeHistory = () => setHistoryOpen(false);

  const addToCart = (item: ItemProps): void =>
    setCartItems((prev) => [...prev, item]);
  const removeFromCart = (item: ItemProps): void =>
    setCartItems((prev) => prev.filter((cartItem) => cartItem.title !== item.title));

  // Function to observe DOM mutations
  const observeMutations = (
    onOpen: () => void,
    setSelectedImage: SetSelectedImageFunction
  ) => {
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((node) => {
          if (node.nodeType === 1) {
            const element = node as HTMLElement;
            const relevantImage = element.querySelector("img:not(.custom-icon)");
            if (relevantImage) {
              console.log("Relevant image found:", relevantImage);
              addIconsToPhotos(element, setSelectedImage, onOpen);
            }
          }
        });
      });
    });

    observer.observe(document.body, { childList: true, subtree: true });
    console.log("MutationObserver initialized.");
  };

  // Function to safely add icons to photos
  const addIconsSafely = (
    onOpen: () => void,
    setSelectedImage: SetSelectedImageFunction
  ) => {
    if ("requestIdleCallback" in window) {
      (window as any).requestIdleCallback(() =>
        addIconsToPhotos(document, setSelectedImage, onOpen)
      );
    } else {
      setTimeout(() => addIconsToPhotos(document, setSelectedImage, onOpen), 50);
    }
    console.log("Safely adding icons to photos.");
  };

  // Run DOM observation logic on component mount
  useEffect(() => {
    try {
      addIconsSafely(onOpen, setSelectedImage);
      observeMutations(onOpen, setSelectedImage);
    } catch (error) {
      console.error("Error in DOM observation logic:", error);
    }
  }, []);

  // Add navigation button to a specific container
  useEffect(() => {
    const container = document.getElementById("wannabe-nav-item");
    if (container) {
      const imgButton = document.createElement("img");
      imgButton.src = "../assets/WannaBeIcon2.png";
      imgButton.style.cursor = "pointer";
      imgButton.style.height = "20px";
      imgButton.style.width = "80px";

      container.appendChild(imgButton);
      console.log("Button added to navigation item.");

      const handleClick = () => openHistory();
      imgButton.addEventListener("click", handleClick);

      return () => {
        imgButton.removeEventListener("click", handleClick);
        container.removeChild(imgButton);
        console.log("Button removed from navigation item.");
      };
    } else {
      console.warn("Navigation container not found for button injection.");
    }
  }, []);

  return (
    <>
      {/* Main Modals */}
      <WannaBeModal
        isOpen={isOpen}
        onClose={onClose}
        selectedImage={selectedImage}
        addToCart={addToCart}
        removeFromCart={removeFromCart}
        cartItems={cartItems}
      />
      <CartModal
        isOpen={cartOpen}
        onClose={closeCart}
        cartItems={cartItems}
        removeFromCart={removeFromCart}
      />
      <HistoryModal
        isOpen={historyOpen}
        onClose={closeHistory}
        addToCart={addToCart}
        removeFromCart={removeFromCart}
        cartItems={cartItems}
        modals={mockModals}
      />

      {/* Floating Cart Button */}
      <FloatingDraggableBookmarks
        cartItemsCount={cartItems.length}
        onClick={openCart}
      />
    </>
  );
};

export default App;
