import React, { useEffect, useState } from "react";
import { Flex, Image, Spinner, Box, Text } from "@chakra-ui/react";
import { X } from "lucide-react";
import { ItemList } from "./ItemList";
import { fetchModalContent } from "../functions/fetchModalContent";
import { ItemProps } from "./Item";
import SorryNoResult from "./SorryNoResult";
import WannaBeLogoText from "../assets/WannaBeLogoWithTextColorNoBorder.png";

interface WannaBeModalInnerComponentProps {
  selectedImage: string | null;
  leftItemsDict?: Record<string, ItemProps[]>;
  rightItemsDict?: Record<string, ItemProps[]>;
  addToCart: (item: ItemProps) => void;
  removeFromCart: (item: ItemProps) => void;
  onClose?: () => void;
  showCloseButton?: boolean;
  shouldShowRefreshIcon?: boolean;
  shouldShowBookmarkIcon?: boolean;
  cartItems?: ItemProps[];
}

const WannaBeModalInnerComponent: React.FC<WannaBeModalInnerComponentProps> = ({
  selectedImage,
  addToCart,
  removeFromCart,
  onClose,
  showCloseButton = true,
  leftItemsDict,
  rightItemsDict,
  shouldShowRefreshIcon,
  shouldShowBookmarkIcon,
  cartItems,
}) => {
  const [modalContent, setModalContent] = useState<Record<string, ItemProps[]>>(
    {}
  );
  const [imageSize, setImageSize] = useState({ width: 0, height: 0 });
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    if (!selectedImage) return;

    const fetchData = async () => {
      setLoading(true);
      try {
        const data = await fetchModalContent(selectedImage);
        setModalContent(data || {});
      } catch (error) {
        console.error("Error fetching modal content:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [selectedImage]);

  const handleImageLoad = (
    event: React.SyntheticEvent<HTMLImageElement, Event>
  ) => {
    const { naturalWidth, naturalHeight } = event.currentTarget;
    setImageSize({ width: naturalWidth, height: naturalHeight });
  };

  const isImageWide = imageSize.width > imageSize.height;
  const isEmptyContent = Object.values(modalContent).every(
    (itemList) => !itemList || itemList.length === 0
  );

  return (
    <Flex direction="column" width="100%">
      {/* Header */}
      <Flex
        as="header"
        alignItems="center"
        justifyContent="center"
        boxShadow="md"
        p={2}
        position="relative"
      >
        <Text
          fontSize="xl"
          fontWeight="extrabold"
          textTransform="uppercase"
          fontFamily="'Heebo', sans-serif"
        >
          Here's What we Found for you
        </Text>
        {showCloseButton && onClose && (
          <Box
            as="button"
            onClick={onClose}
            position="absolute"
            right="16px"
            top="8px"
            width="32px"
            height="32px"
            borderRadius="full"
            display="flex"
            alignItems="center"
            justifyContent="center"
            bg="white"
            color="purple.700"
            boxShadow="lg"
            _hover={{ bg: "purple.50", transform: "scale(1.1)" }}
            _active={{ bg: "purple.100", transform: "scale(0.95)" }}
          >
            <X size={20} />
          </Box>
        )}
      </Flex>

      <Flex
        height="100%"
        overflowX="hidden"
        gap={2}
        p={6}
        direction={["column", "row"]}
      >
        {/* Left content */}
        <Flex
          width={["100%", isImageWide ? "25%" : "33%"]}
          overflowY="auto"
          paddingRight={2}
          paddingLeft={2}
          paddingBottom={2}
          direction="column"
        >
          <Text
            fontSize="24px"
            fontWeight="extrabold"
            mb={4}
            color="black.900"
            textAlign="center"
            fontFamily="'Heebo', sans-serif"
          >
            Be Most:
          </Text>
          {loading ? (
            <Box
              textAlign="center"
              width="100%"
              height="100%"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              <Spinner size="xl" color="blue.500" />
            </Box>
          ) : isEmptyContent ? (
            <SorryNoResult />
          ) : (
            <ItemList
              itemsDict={leftItemsDict ?? modalContent}
              iconDirection="right"
              addToCart={addToCart}
              removeFromCart={removeFromCart}
              shouldShowRefreshIcon={shouldShowRefreshIcon}
              shouldShowBookmarkIcon={shouldShowBookmarkIcon}
              cartItems={cartItems}
            />
          )}
        </Flex>

        {/* Center image */}
        <Flex
          width={["100%", isImageWide ? "50%" : "34%"]}
          borderRadius="lg"
          overflow="hidden"
          boxShadow="md"
          position="relative"
          justifyContent="center"
        >
          {selectedImage && (
            <Image
              src={selectedImage}
              height="100%"
              width="100%"
              objectFit="cover"
              onLoad={handleImageLoad}
            />
          )}
        </Flex>

        {/* Right content */}
        <Flex
          width={["100%", isImageWide ? "25%" : "33%"]}
          overflowY="auto"
          paddingRight={2}
          paddingLeft={2}
          paddingBottom={2}
          direction="column"
        >
          <Text
            fontSize="24px"
            fontWeight="extrabold"
            mb={4}
            color="black.900"
            textAlign="center"
            fontFamily="'Heebo', sans-serif"
          >
            Be Like:
          </Text>
          {loading ? (
            <Box
              textAlign="center"
              width="100%"
              height="100%"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              <Spinner size="xl" color="blue.500" />
            </Box>
          ) : isEmptyContent ? (
            <SorryNoResult />
          ) : (
            <ItemList
              itemsDict={rightItemsDict ?? modalContent}
              iconDirection="right"
              addToCart={addToCart}
              removeFromCart={removeFromCart}
              shouldShowRefreshIcon={shouldShowRefreshIcon}
              shouldShowBookmarkIcon={shouldShowBookmarkIcon}
              cartItems={cartItems}
            />
          )}
        </Flex>
      </Flex>

      {/* Footer */}
      <Flex
        borderTop="1px"
        borderColor="gray.200"
        p={4}
        alignItems="center"
        justifyContent="center"
        bg="gray.50"
        gap={2}
      >
        <Text fontSize="sm" color="gray.600" fontWeight="medium">
          Powered by
        </Text>
        <Image
          src={WannaBeLogoText}
          height="22px"
          width="80px"
          display="inline"
        />
      </Flex>
    </Flex>
  );
};

export default WannaBeModalInnerComponent;
