import { Flex } from "@chakra-ui/react";
import { Item, ItemProps } from "./Item";

interface ItemListProps {
  itemsDict: Record<string, ItemProps[]>;
  iconDirection: "left" | "right";
  addToCart: (item: ItemProps) => void;
  removeFromCart: (item: ItemProps) => void;
  shouldShowTrashIcon?: boolean;
  shouldShowRefreshIcon?: boolean;
  shouldShowBookmarkIcon?: boolean;
  cartItems?: ItemProps[];
}

export const ItemList: React.FC<ItemListProps> = ({
  itemsDict,
  iconDirection,
  addToCart,
  removeFromCart,
  shouldShowTrashIcon,
  shouldShowRefreshIcon,
  shouldShowBookmarkIcon,
  cartItems,
}) => {
  console.log("ItemList itemsDict:", itemsDict);

  return (
    <Flex direction={"column"} flex={1}>
      <Flex gap={2.5} direction={"column"} height="100%">
        {Object.entries(itemsDict)
          .filter(([, itemList]) => itemList && itemList.length > 0)
          .map(([key, itemList]) => (
            <Item
              key={key}
              items={itemList}
              iconDirection={iconDirection}
              addToCart={addToCart}
              removeFromCart={removeFromCart}
              shouldShowTrashIcon={shouldShowTrashIcon}
              shouldShowRefreshIcon={shouldShowRefreshIcon}
              shouldShowBookmarkIcon={shouldShowBookmarkIcon}
              cartItems={cartItems}
            />
          ))}
      </Flex>
    </Flex>
  );
};
