export async function fetchModalContent(imageUrl: string): Promise<any> {
  console.log("Requesting content for image URL:", imageUrl); // Log the URL to debug the API call

  try {
    const response = await fetch(
      "http://localhost:8083/getFashionItemsByCategoriesImageTextBased?image_url=" +
        imageUrl +
        "&prompts=jacket, glasses, pants, hand bag",
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!response.ok) {
      // The response status is not 200, handle this as an error
      console.error("Error: Bad response status", response.status);
      throw new Error(`Server returned status ${response.status}`);
    }

    const data = await response.json();

    // Log the raw response data
    console.log("Raw fetched data:", data);

    // Check if the API returned an empty object
    if (Object.keys(data).length === 0) {
      console.warn("Warning: Received empty data from API");
      // throw new Error("Received empty data from API");
      throw new Error("Received empty data from API");
    }

    return data;
  } catch (error) {
    // If an error occurs, log it and return mock data
    console.error("Error fetching modal content:", error);

    // Return mock data in case of failure
    return mockItemDictOne;
  }
}
