import React from "react";
import MainModalWrapper from "./MainModalWrapper";
import WannaBeModalInnerComponent from "./WannaBeModalInnerComponent";
import { ItemProps } from "./Item";

interface WannaBeModalProps {
  isOpen: boolean;
  onClose: () => void;
  selectedImage: string | null;
  addToCart: (item: ItemProps) => void;
  removeFromCart: (item: ItemProps) => void;
  cartItems?: ItemProps[];
}

const WannaBeModal: React.FC<WannaBeModalProps> = ({
  isOpen,
  onClose,
  selectedImage,
  addToCart,
  removeFromCart,
  cartItems,
}) => (
  <MainModalWrapper isOpen={isOpen} onClose={onClose} maxWidth="1400px">
    <WannaBeModalInnerComponent
      selectedImage={selectedImage}
      addToCart={addToCart}
      removeFromCart={removeFromCart}
      onClose={onClose}
      shouldShowRefreshIcon={true}
      shouldShowBookmarkIcon={true}
      cartItems={cartItems}
    />
  </MainModalWrapper>
);

export default WannaBeModal;
