import { Flex, Text, Image, Icon, Box } from "@chakra-ui/react";
import { useEffect, useState } from "react";
import RefreshIcon from "../assets/RefreshIcon.png";
import BookmarkIcon from "../assets/BookmarkIcon.png";
import BookmarkIconPushed from "../assets/BookmarkIconPushed.png";
import { ReactComponent as TrashIcon } from "../assets/icons8-trash.svg";

export interface ItemProps {
  brand: string;
  title: string;
  price: number;
  preview_url: string;
  buy_link: string;
}

interface ItemComponentProps {
  items: ItemProps[];
  iconDirection: "left" | "right";
  addToCart: (item: ItemProps) => void;
  removeFromCart: (item: ItemProps) => void;
  shouldShowTrashIcon?: boolean;
  shouldShowRefreshIcon?: boolean;
  shouldShowBookmarkIcon?: boolean;
  cartItems?: ItemProps[];
}

export const Item: React.FC<ItemComponentProps> = ({
  items,
  iconDirection,
  addToCart,
  removeFromCart,
  shouldShowTrashIcon,
  shouldShowRefreshIcon,
  shouldShowBookmarkIcon,
  cartItems = [],
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isFading, setIsFading] = useState(false);
  const currentItem = items[currentIndex];
  const [isBookmarked, setIsBookmarked] = useState(
    cartItems.some((item) => item.title === currentItem.title)
  );

  // Sync isBookmarked with cartItems prop
  useEffect(() => {
    setIsBookmarked(cartItems.some((item) => item.title === currentItem.title));
  }, [cartItems, currentItem]);

  const handleRefresh = (e: React.MouseEvent) => {
    e.stopPropagation(); // Prevent triggering parent click
    setIsFading(true);
    setTimeout(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % items.length);
      setIsFading(false);
    }, 300);
  };

  const handleBookmarkToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (isBookmarked) {
      removeFromCart(currentItem);
    } else {
      addToCart(currentItem);
    }
    // State will be updated via useEffect when cartItems prop changes
  };

  const handleItemClick = () => {
    window.open(currentItem.buy_link, "_blank", "noopener,noreferrer");
  };

  return (
    <Flex
      direction="row"
      align="center"
      _hover={{
        transform: "scale(1.03)",
        transition: "all 0.3s ease-in-out",
        boxShadow: "lg",
      }}
      bgGradient="linear-gradient(120deg, rgba(234, 234, 234, 1) 0%, rgba(255, 255, 255, 1) 100%)"
      opacity={isFading ? 0 : 1}
      transition="opacity 0.3s ease"
      height="100%"
      p={2}
      border="1px solid rgba(234, 234, 234, 1)"
      boxShadow="0px 4px 6px rgba(0, 0, 0, 0.1)"
      position="relative"
      cursor="pointer"
      onClick={handleItemClick}
      borderRadius="19px"
    >
      {/* Image */}
      <Box
        boxSize="85px"
        display="flex"
        alignItems="center"
        overflow="hidden"
        color="white"
        borderRadius="19px"
      >
        <Image src={currentItem.preview_url} alt={currentItem.brand} />
      </Box>

      {/* Text */}
      <Flex direction="column" ml={3} flex={1}>
        <Text fontWeight="semibold" fontSize="18px" textAlign="left" mb={0}>
          {currentItem.brand}
        </Text>
        <Text fontSize="18px" textAlign="left" mb={0}>
          {currentItem.title}
        </Text>
        <Text color="gray.500" fontSize="16px" mb={0}>
          ${currentItem.price}
        </Text>
      </Flex>

      {/* Icons */}
      <Flex
        ml={8}
        alignItems="center"
        position="absolute"
        bottom="14px"
        right="10px"
      >
        {iconDirection === "right" && (
          <>
            {shouldShowRefreshIcon && (
              <Image
                src={RefreshIcon}
                boxSize="18px"
                onClick={handleRefresh}
                cursor="pointer"
                ml={2}
              />
            )}
            {shouldShowBookmarkIcon && (
              <Image
                src={isBookmarked ? BookmarkIconPushed : BookmarkIcon}
                onClick={handleBookmarkToggle}
                cursor="pointer"
                width="18px"
                height="20px"
                ml={2}
                style={{
                  filter: isBookmarked ? "none" : "grayscale(100%)",
                }}
              />
            )}
            {shouldShowTrashIcon && (
              <Icon
                as={TrashIcon}
                boxSize={4}
                onClick={(e) => {
                  e.stopPropagation();
                  removeFromCart(currentItem);
                }}
                cursor="pointer"
                ml={2}
              />
            )}
          </>
        )}
      </Flex>
    </Flex>
  );
};
