import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  Flex,
  Text,
  Box,
  Divider,
  Image,
} from "@chakra-ui/react";
import { ItemList } from "./ItemList";
import BookmarkIcon from "../assets/BookmarkIcon.png";
import { ItemProps } from "./Item";
import { X } from "lucide-react";

interface CartModalProps {
  isOpen: boolean;
  onClose: () => void;
  cartItems: ItemProps[];
  removeFromCart: (item: ItemProps) => void;
}

export const CartModal: React.FC<CartModalProps> = ({
  isOpen,
  onClose,
  cartItems,
  removeFromCart,
}) => {
  const itemsDict = cartItems.reduce<Record<string, ItemProps[]>>(
    (acc, item) => {
      acc[item.title] = acc[item.title] ? [...acc[item.title], item] : [item];
      return acc;
    },
    {}
  );

  const total = cartItems.reduce((sum, item) => sum + item.price, 0);

  return (
    <Modal isOpen={isOpen} onClose={onClose} size="xl">
      <ModalOverlay />
      <ModalContent width="500px" marginY="10vh">
        {/* Header */}
        <Flex as="header" justifyContent="center" boxShadow="md" p={3.5}>
          <Text
            fontSize="l"
            fontWeight="extrabold"
            textTransform="uppercase"
            letterSpacing="widest"
            fontFamily="'Heebo', sans-serif"
          >
            Your Saved Items
          </Text>

          {/* Close Button */}
          <Box
            as="button"
            onClick={onClose}
            position="absolute"
            right="12px"
            top="12px"
            width="32px"
            height="32px"
            borderRadius="full"
            display="flex"
            alignItems="center"
            justifyContent="center"
            bg="white"
            color="purple.700"
            boxShadow="lg"
            transition="all 0.2s"
            _hover={{
              bg: "purple.50",
              transform: "scale(1.1)",
            }}
            _active={{
              bg: "purple.100",
              transform: "scale(0.95)",
            }}
          >
            <X size={20} />
          </Box>
        </Flex>
        <ModalBody p={4}>
          <Flex direction="column">
            {cartItems.length === 0 ? (
              <Flex
                direction="column"
                align="center"
                justify="center"
                flex={1}
                pt={10}
                pb={10}
              >
                <Image
                  src={BookmarkIcon}
                  cursor="pointer"
                  width="50px"
                  height="50px"
                  ml={2}
                />{" "}
                <Text color="gray.500" mt={4}>
                  Looks like you don't have any saved items
                </Text>
              </Flex>
            ) : (
              <Box
                flex={1}
                pl={2.5}
                pb={4}
                pr={2.5}
                maxHeight="400px"
                overflowY="auto"
                css={{
                  "&::-webkit-scrollbar": {
                    width: "8px",
                  },
                  "&::-webkit-scrollbar-thumb": {
                    background: "#888",
                    borderRadius: "4px",
                  },
                  "&::-webkit-scrollbar-thumb:hover": {
                    background: "#555",
                  },
                }}
              >
                <ItemList
                  itemsDict={itemsDict}
                  iconDirection="right"
                  addToCart={() => {}}
                  removeFromCart={removeFromCart}
                  shouldShowTrashIcon={true}
                />
              </Box>
            )}
            <Box>
              <Divider mb={4} />
              <Flex justify="space-between" align="center" mb={2}>
                <Box>
                  <Text fontWeight="medium">Total Amount</Text>
                  <Text fontSize="sm" color="gray.500">
                    {cartItems.length} items
                  </Text>
                </Box>
                <Text fontSize="2xl" fontWeight="bold">
                  $
                  {total.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}
                </Text>
              </Flex>
            </Box>
          </Flex>
        </ModalBody>
      </ModalContent>
    </Modal>
  );
};

export default CartModal;
