import { processImage } from "./processImage";

type SetSelectedImageFunction = React.Dispatch<
  React.SetStateAction<string | null>
>;

export const addIconsToPhotos = (
  target: ParentNode = document,
  setSelectedImage: SetSelectedImageFunction,
  onOpen: () => void
): void => {
  const images = target.querySelectorAll(
    '[data-wannabe-process="true"]'
  ) as NodeListOf<HTMLImageElement>;
  console.log(`Found ${images.length} images.`);
  images.forEach((img) => {
    if (img.dataset.iconAdded === "true") return;

    if (img.complete) {
      processImage(img, setSelectedImage, onOpen);
    } else {
      img.onload = () => processImage(img, setSelectedImage, onOpen);
    }
  });
};
