type SetSelectedImageFunction = React.Dispatch<
  React.SetStateAction<string | null>
>;

export const processImage = (
  img: HTMLImageElement,
  setSelectedImage: SetSelectedImageFunction,
  onOpen: () => void
) => {
  img.dataset.iconAdded = "true";

  let container = img.parentElement;
  const isContainerCreated = container?.dataset.imageContainer === "true";

  if (!isContainerCreated) {
    container = document.createElement("div");
    container.style.position = "relative";
    container.style.display = "inline-block";
    container.style.width = `${img.width}px`;
    container.style.height = `${img.height}px`;
    container.dataset.imageContainer = "true";

    if (img.parentNode) {
      img.parentNode.insertBefore(container, img);
      container.appendChild(img);
    }
  }

  const icon = document.createElement("img");
  icon.src = "../assets/WannaBeIconNoColor.png";
  icon.className = "custom-icon";
  icon.style.position = "absolute";
  icon.style.bottom = "6px";
  icon.style.right = "6px";
  icon.style.width = "55px";
  icon.style.height = "75px";
  icon.style.cursor = "pointer";
  icon.style.zIndex = "10";

  icon.addEventListener("click", (event) => {
    event.stopImmediatePropagation(); // Prevent any other click handlers on the icon
    console.log("Icon clicked for image:", img.src);
    setSelectedImage(img.src);
    onOpen();
  });

  container?.appendChild(icon);
};
